@import "__resets";

@mixin flex {
  display: flex;
  flex-wrap: wrap;
}
.assets-holder {
  @include flex();
  height: 100vh;
  background: rgba(116, 114, 114, 0.5);
  overflow: scroll;
  justify-content: center;
  #assets {
    font-family: "Lucida Sans", "Lucida Sans Regular", "Lucida Grande",
      "Lucida Sans Unicode", Geneva, Verdana, sans-serif;
    background: rgb(26, 25, 25);
    height: 15em;
    width: 22em;
    margin: 5em 2em 2em 2em;
    display: flex;
    align-items: center;
    flex-direction: column;
    color: white;
    box-shadow: 5px 5px 5px wheat, 1px 1px 1px white, 25px 25px 25px black;
    &:hover {
      cursor: pointer;
      box-shadow: none;
    }
    p {
      padding: 0.4em;
    }
    #logo {
      border-radius: 50%;
      height: 2em;
      width: 2em;
      margin-left: 0em 0 0 -9em;
    }
    #price {
      color: springgreen;
    }
    #supply {
      color: rgba(126, 128, 231, 0.966);
    }
  }
  

  footer {
    background: rgb(41, 39, 39);
    margin: 0;
    align-self: flex-start;
  }
}

::-webkit-scrollbar {
  display: none;
}
